/* purgecss start ignore */

.calendar{
  width:381px
}

.calendar .active{
  display:flex;
  flex-direction:column
}

.calendar .active .days{
  margin-bottom:-1px;
  display:flex;
  justify-content:center
}

@media (min-width: 640px){
  .calendar .active .days{
    margin-left:0.75rem
  }
}

.calendar .active .days .days-container{
  width:315px;
  height:60px;
  overflow:hidden
}

.calendar .active .days .days-container .days-wrapper{
  scroll-behavior:smooth;
  position:relative;
  z-index:10;
  margin-right:auto;
  display:flex;
  overflow-y:hidden;
  overflow-x:scroll
}

.calendar .active .days .days-container .days-wrapper .selector{
  width:45px;
  position:absolute;
  z-index:0;
  height:100%;
  border-width:1px;
  border-bottom-width:0px;
  --tw-border-opacity:1;
  border-color:rgba(156, 163, 175, var(--tw-border-opacity));
  --tw-bg-opacity:1;
  background-color:rgba(249, 250, 251, var(--tw-bg-opacity));
  transition-duration:300ms
}

.calendar .active .days .days-container .days-wrapper .day{
  min-width:45px;
  height:60px;
  position:relative;
  z-index:10;
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding-top:0.25rem;
  padding-bottom:0.25rem;
  text-align:center;
  transition-duration:500ms
}

.calendar .active .days .days-container .days-wrapper .day.selected{
  pointer-events:none
}

.calendar .active .days .days-container .days-wrapper .day .content:hover{
  --tw-bg-opacity:1;
  background-color:rgba(255, 255, 255, var(--tw-bg-opacity))
}

.calendar .active .days .days-container .days-wrapper .day .content{
  transition-duration:500ms
}

.calendar .active .days .days-container .days-wrapper .day *{
  pointer-events:none
}

.calendar .active .days .days-container .days-wrapper .day.today:after{
  content:"";
  height:6px;
  width:6px;
  left:calc(50% - 3px);
  position:absolute;
  bottom:0.25rem;
  border-radius:9999px;
  --tw-bg-opacity:1;
  background-color:rgba(59, 130, 246, var(--tw-bg-opacity))
}

.calendar .active .slots{
  border-width:1px;
  --tw-border-opacity:1;
  border-color:rgba(156, 163, 175, var(--tw-border-opacity));
  padding:0.75rem;
  display:grid;
  grid-template-columns:repeat(5, minmax(0, 1fr));
  gap:0.5rem
}

.calendar .active .slots .slot{
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  --tw-bg-opacity:1;
  background-color:rgba(107, 114, 128, var(--tw-bg-opacity))
}

.calendar .active .slots .slot:hover{
  --tw-bg-opacity:1;
  background-color:rgba(75, 85, 99, var(--tw-bg-opacity))
}

.calendar .active .slots .slot{
  padding-left:0.5rem;
  padding-right:0.5rem;
  padding-top:0.25rem;
  padding-bottom:0.25rem;
  text-align:center;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity));
  --tw-ring-offset-shadow:var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
  --tw-ring-shadow:var(--tw-ring-inset) 0 0 0 calc(0px + var(--tw-ring-offset-width)) var(--tw-ring-color);
  box-shadow:var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
  --tw-ring-opacity:1;
  --tw-ring-color:rgba(59, 130, 246, var(--tw-ring-opacity));
  transition-duration:200ms
}

.calendar .active .slots .slot.selected{
  --tw-ring-offset-shadow:var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
  --tw-ring-shadow:var(--tw-ring-inset) 0 0 0 calc(2px + var(--tw-ring-offset-width)) var(--tw-ring-color);
  box-shadow:var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000)
}

.calendar .control{
  width:30px;
  display:none;
  margin-left:0.75rem;
  margin-right:0.75rem;
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  align-items:center;
  justify-content:center
}

@media (min-width: 640px){
  .calendar .control{
    display:flex
  }
}

.calendar .control .icon{
  width:2rem;
  --tw-translate-x:0;
  --tw-translate-y:0;
  --tw-rotate:0;
  --tw-skew-x:0;
  --tw-skew-y:0;
  --tw-scale-x:1;
  --tw-scale-y:1;
  transform:translateX(var(--tw-translate-x)) translateY(var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
  border-radius:9999px;
  --tw-bg-opacity:1;
  background-color:rgba(156, 163, 175, var(--tw-bg-opacity));
  padding:0.25rem;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity));
  transition-duration:300ms
}

.calendar .control .icon.back{
  --tw-rotate:180deg
}

.calendar .empty{
  text-align:center;
  font-size:1.25rem;
  line-height:1.75rem
}

/* purgecss end ignore */